'use client';

import { useState } from 'react';
import { motion } from 'framer-motion';
import { useMilestones } from '@/context/MilestoneContext';
import { CheckCircleIcon, ClockIcon, CalendarIcon, BookOpenIcon, TrophyIcon } from '@heroicons/react/24/outline';

interface MilestoneDetailsProps {
  milestoneId: string;
}

export default function MilestoneDetails({ milestoneId }: MilestoneDetailsProps) {
  const { milestones, updateSubTask, addSubTask, addResource, addAchievement, updateTimeTracking } = useMilestones();
  const milestone = milestones.find(m => m.id === milestoneId);
  const [newSubTask, setNewSubTask] = useState('');
  const [newResource, setNewResource] = useState('');
  const [newAchievement, setNewAchievement] = useState('');
  const [hoursToAdd, setHoursToAdd] = useState(0);

  if (!milestone) return null;

  const handleSubTaskToggle = (subTaskId: string) => {
    const subTask = milestone.subTasks.find(t => t.id === subTaskId);
    if (subTask) {
      updateSubTask(milestoneId, subTaskId, !subTask.completed);
    }
  };

  const handleAddSubTask = () => {
    if (newSubTask.trim()) {
      addSubTask(milestoneId, {
        id: `${milestoneId}-${Date.now()}`,
        title: newSubTask,
        completed: false,
        estimatedHours: 0,
      });
      setNewSubTask('');
    }
  };

  const handleAddResource = () => {
    if (newResource.trim()) {
      addResource(milestoneId, newResource);
      setNewResource('');
    }
  };

  const handleAddAchievement = () => {
    if (newAchievement.trim()) {
      addAchievement(milestoneId, newAchievement);
      setNewAchievement('');
    }
  };

  const handleAddHours = () => {
    if (hoursToAdd > 0) {
      updateTimeTracking(milestoneId, hoursToAdd);
      setHoursToAdd(0);
    }
  };

  const progressPercentage = milestone.subTasks.length > 0
    ? Math.round((milestone.subTasks.filter(t => t.completed).length / milestone.subTasks.length) * 100)
    : 0;

  return (
    <div className="space-y-8">
      {/* Progress Overview */}
      <div className="bg-background-darker p-6 rounded-xl border border-gray-800">
        <h3 className="text-xl font-semibold text-white mb-4">Progress Overview</h3>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          <div className="p-4 rounded-lg bg-white/5 border border-white/10">
            <div className="flex items-center gap-2 mb-2">
              <ClockIcon className="w-5 h-5 text-blue-400" />
              <span className="text-sm text-text-secondary">Time Spent</span>
            </div>
            <div className="text-2xl font-bold text-white">{milestone.completedHours || 0}h</div>
            <div className="text-sm text-text-secondary">of estimated time</div>
          </div>
          <div className="p-4 rounded-lg bg-white/5 border border-white/10">
            <div className="flex items-center gap-2 mb-2">
              <CheckCircleIcon className="w-5 h-5 text-emerald-400" />
              <span className="text-sm text-text-secondary">Tasks Completed</span>
            </div>
            <div className="text-2xl font-bold text-white">
              {milestone.subTasks.filter(t => t.completed).length}/{milestone.subTasks.length}
            </div>
            <div className="text-sm text-text-secondary">{progressPercentage}% complete</div>
          </div>
          <div className="p-4 rounded-lg bg-white/5 border border-white/10">
            <div className="flex items-center gap-2 mb-2">
              <TrophyIcon className="w-5 h-5 text-amber-400" />
              <span className="text-sm text-text-secondary">Achievements</span>
            </div>
            <div className="text-2xl font-bold text-white">{milestone.achievements.length}</div>
            <div className="text-sm text-text-secondary">milestones reached</div>
          </div>
        </div>
      </div>

      {/* Tasks */}
      <div className="bg-background-darker p-6 rounded-xl border border-gray-800">
        <h3 className="text-xl font-semibold text-white mb-4">Tasks</h3>
        <div className="space-y-4">
          {milestone.subTasks.map((task) => (
            <motion.div
              key={task.id}
              whileHover={{ scale: 1.02 }}
              className={`p-4 rounded-lg border ${
                task.completed
                  ? 'bg-emerald-500/10 border-emerald-500/30'
                  : 'bg-white/5 border-white/10'
              }`}
            >
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-3">
                  <button
                    onClick={() => handleSubTaskToggle(task.id)}
                    className={`w-5 h-5 rounded-full border-2 flex items-center justify-center ${
                      task.completed
                        ? 'bg-emerald-500 border-emerald-500'
                        : 'border-white/30'
                    }`}
                  >
                    {task.completed && <CheckCircleIcon className="w-4 h-4 text-white" />}
                  </button>
                  <span className={`text-white ${task.completed ? 'line-through opacity-50' : ''}`}>
                    {task.title}
                  </span>
                </div>
                <span className="text-sm text-text-secondary">{task.estimatedHours}h</span>
              </div>
            </motion.div>
          ))}
        </div>
        <div className="mt-4 flex gap-2">
          <input
            type="text"
            value={newSubTask}
            onChange={(e) => setNewSubTask(e.target.value)}
            placeholder="Add new task"
            className="flex-1 bg-white/5 border border-white/10 rounded-lg px-4 py-2 text-white placeholder:text-white/50"
          />
          <button
            onClick={handleAddSubTask}
            className="px-4 py-2 bg-primary hover:bg-primary/90 text-white rounded-lg transition-colors"
          >
            Add Task
          </button>
        </div>
      </div>

      {/* Resources */}
      <div className="bg-background-darker p-6 rounded-xl border border-gray-800">
        <h3 className="text-xl font-semibold text-white mb-4">Learning Resources</h3>
        <div className="space-y-3">
          {milestone.resources.map((resource, index) => (
            <div
              key={index}
              className="flex items-center gap-3 p-3 rounded-lg bg-white/5 border border-white/10"
            >
              <BookOpenIcon className="w-5 h-5 text-blue-400" />
              <span className="text-white">{resource}</span>
            </div>
          ))}
        </div>
        <div className="mt-4 flex gap-2">
          <input
            type="text"
            value={newResource}
            onChange={(e) => setNewResource(e.target.value)}
            placeholder="Add new resource"
            className="flex-1 bg-white/5 border border-white/10 rounded-lg px-4 py-2 text-white placeholder:text-white/50"
          />
          <button
            onClick={handleAddResource}
            className="px-4 py-2 bg-primary hover:bg-primary/90 text-white rounded-lg transition-colors"
          >
            Add Resource
          </button>
        </div>
      </div>

      {/* Achievements */}
      <div className="bg-background-darker p-6 rounded-xl border border-gray-800">
        <h3 className="text-xl font-semibold text-white mb-4">Achievements</h3>
        <div className="space-y-3">
          {milestone.achievements.map((achievement, index) => (
            <div
              key={index}
              className="flex items-center gap-3 p-3 rounded-lg bg-white/5 border border-white/10"
            >
              <TrophyIcon className="w-5 h-5 text-amber-400" />
              <span className="text-white">{achievement}</span>
            </div>
          ))}
        </div>
        <div className="mt-4 flex gap-2">
          <input
            type="text"
            value={newAchievement}
            onChange={(e) => setNewAchievement(e.target.value)}
            placeholder="Add new achievement"
            className="flex-1 bg-white/5 border border-white/10 rounded-lg px-4 py-2 text-white placeholder:text-white/50"
          />
          <button
            onClick={handleAddAchievement}
            className="px-4 py-2 bg-primary hover:bg-primary/90 text-white rounded-lg transition-colors"
          >
            Add Achievement
          </button>
        </div>
      </div>

      {/* Time Tracking */}
      <div className="bg-background-darker p-6 rounded-xl border border-gray-800">
        <h3 className="text-xl font-semibold text-white mb-4">Time Tracking</h3>
        <div className="flex items-center gap-4">
          <input
            type="number"
            value={hoursToAdd}
            onChange={(e) => setHoursToAdd(Number(e.target.value))}
            placeholder="Hours to add"
            className="w-32 bg-white/5 border border-white/10 rounded-lg px-4 py-2 text-white"
            min="0"
          />
          <button
            onClick={handleAddHours}
            className="px-4 py-2 bg-primary hover:bg-primary/90 text-white rounded-lg transition-colors"
          >
            Add Hours
          </button>
        </div>
        <div className="mt-4 text-sm text-text-secondary">
          Total time spent: {milestone.completedHours || 0} hours
        </div>
      </div>
    </div>
  );
} 